<template>
  <div class="homePage" id="myaudio">
    <audio autoplay>
      <source src="/static/audio/music.mp3" type="audio/mpeg" />
    </audio>
    <swiper :options="swiperOptionTop" ref="mySwiperTop" class="topBox">
      <swiper-slide v-for="bg in bgs" :class="[bg]" :key="bg.id">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination">
      </div>
      <div class="swiper-button-prev" slot="button-prev">
      </div>
      <div class="swiper-button-next" slot="button-next">
      </div>
    </swiper>
    <swiper :options="swiperOptionBottom" ref="mySwiperBottom" class="bottomBox">
      <swiper-slide v-for="bg in bgs" :class="[bg]" :key="bg.id"></swiper-slide>
    </swiper>
    <div class="person animated tada">
      <div class="personImg"></div>
      <h1>Hello,I'm Lue</h1>
      <h2>我叫路耀广</h2>
      <h2>一名前端工程师</h2>
      <a href="#" class="enterBtn" @click.prevent="goPerson">我的主页</a>
    </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "homePage",
  data() {
    return {
      swiperOptionTop: {
        spaceBetween: 10,
        loop: true,
        loopedSlides: 5,
        grabCursor: true,
        effect: "fade",
        speed: 2000,
        parallax: true,
        autoplay: {
          delay: 5000 //5秒切换一次
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        pagination: {
          el: ".swiper-pagination",
          type: "bullets",
          clickable: true
        }
      },
      swiperOptionBottom: {
        spaceBetween: 10,
        slidesPerView: 4,
        touchRatio: 0.2,
        loop: true,
        grabCursor: true,
        loopedSlides: 5, //looped slides should be the same
        slideToClickedSlide: true
      },
      bgs: ["bg01", "bg02", "bg03", "bg04", "bg05"]
    };
  },
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    // mySwiperTop() {
    //   return this.$refs.mySwiperTop.swiper;
    // },
    // mySwiperBottom() {
    //   return this.$refs.mySwiperBottom.swiper;
    // }
  },
  mounted() {
    const swiperTop = this.$refs.mySwiperTop.swiper;
    const swiperBottom = this.$refs.mySwiperBottom.swiper;
    swiperTop.controller.control = swiperBottom;
    swiperBottom.controller.control = swiperTop;
  },
  methods: {
    goPerson() {
      this.$router.push("/person");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
/*animta*/
.pulse {
  -webkit-animation-name: pulse;
  animation-name: pulse;
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
/*owner*/
.enterBtn {
  padding: 10px 15px;
  background: #665353;
  border-radius: 5px;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  margin-top: 10px;
  &:hover {
    background: #918282;
  }
}
.homePage {
  height: 100%;
  background-color: #888;
  .topBox {
    height: 78%;
  }
  .bottomBox {
    margin-top: 2%;
    height: 18%;
    opacity: 0.3;
  }
}
.swiper-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
.swiper-slide {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 0;
}
.gallery-top {
  height: 80%;
  width: 100%;
}
.gallery-thumbs {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
}
.gallery-thumbs .swiper-slide {
  height: 100%;
  opacity: 0.5;
}
.gallery-thumbs .swiper-slide-active {
  opacity: 1;
}
.bg01 {
  background-image: url("/static/imgs/bg01.jpeg");
}
.bg02 {
  background-image: url("/static/imgs/bg02.jpeg");
}
.bg03 {
  background-image: url("/static/imgs/bg03.jpeg");
}
.bg04 {
  background-image: url("/static/imgs/bg04.jpeg");
}
.bg05 {
  background-image: url("/static/imgs/bg05.jpeg");
}
.person {
  position: fixed;
  width: 40%;
  top: 10%;
  left: 30%;
  z-index: 99;
  text-align: center;
  color: #222;
  h1 {
    font-size: 24px;
  }
  h2 {
    font-size: 20px;
  }
}
.personImg {
  width: 150px;
  height: 150px;
  display: inline-block;
  background: url("../../static/imgs/luyaoguang.jpeg") no-repeat center center;
  background-size: cover;
  border: 1px solid #665353;
  border-radius: 50%;
  &:hover {
    .animated;
    .pulse;
  }
}
</style>



