<template>
  <div class="personBox">
    <div class="musicIcon bgs"></div>
    <audio autoplay loop id="myaudio">
      <source src="/static/audio/music.mp3" type="audio/mpeg" />
    </audio>
    <swiper :options="swiperOption" ref="mySwiperTop" class="topBox">
      <swiper-slide class="bg01 bgs animated bounceInUp">
        <div class="personImg"></div>
        <div class="personInfo">
          您好,我叫路耀广,26岁,坐标杭州。我并不是计算机专业,但对前端很感兴趣,所以自学前端,今后的职业规划是朝着全栈发展。 <br/> 目前在学React和Vue。写过响应式页面，移动端页面，pc端页面。 <br/> 目前就职于杭州光云科技有限公司。不浮躁，喜欢前端,执行力,学习能力都棒棒哒！
        </div>
      </swiper-slide>
      <swiper-slide class="skillBox">
        <h3 class="skills">Skills</h3>
        <ul class="barUl">
          <li v-for="bar in bars">
            <h4>{{bar.label}}</h4>
            <el-progress :stroke-width="20" :percentage="bar.percentage" :color="bar.color"></el-progress>
          </li>
        </ul>
      </swiper-slide>
      <swiper-slide class="workBox">
        <h3 class="works">Works</h3>
        <ul class="workUl">
          <li v-for="work in works" :key="work.id">
            <div class="workTime">
              <span class="rectIcon"></span>
              <span class="time">{{work.times}}</span>
            </div>
            <div class="workContent">
              <strong>{{work.company}}</strong>
              <em>{{work.job}}</em>
              <p>
                {{work.content}}
              </p>
            </div>
          </li>
        </ul>
      </swiper-slide>
      <!-- <div class="swiper-pagination">
        </div> -->
    </swiper>
  </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "person",
  data() {
    return {
      swiperOption: {
        direction: "vertical",
        slidesPerView: 1,
        spaceBetween: 30,
        mousewheel: true
        // pagination: {
        //   el: ".swiper-pagination",
        //   clickable: true,
        //   bulletElement: "li"
        // }
      },
      bgs: ["bg01", "bg02", "bg03", "bg04", "bg05"],
      bars: [
        { label: "JavaScript", percentage: 80, color: "#1abc9c" },
        { label: "CSS3", percentage: 80, color: "#4094c7" },
        { label: "HTML5", percentage: 70, color: "#E87E04" },
        { label: "Node", percentage: 50, color: "#F56C6C" },
        { label: "Vue", percentage: 70, color: "#67C23A" },
        { label: "React", percentage: 70, color: "#e4297b" }
      ],
      works: [
        {
          times: "2018年3月——至今",
          company: "光云科技有限公司",
          job: "前端工程师",
          content: "负责网店交易前端开发,主要使用vue负责pc端"
        },
        {
          times: "2017年6月——2018年3月",
          company: "浙江网新恒天",
          job: "前端工程师",
          content: "主要负责杭州银行票交所直连系统前端页面开发。"
        },
        {
          times: "2014年6月——2017年6月",
          company: "上海安硕科技有限公司",
          job: "前端工程师",
          content: "主要负责过指尖租赁,恒大金服等项目前端页面开发。"
        }
      ]
    };
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>
<style lang="less" scoped>
.workBox {
  background: #d8d4d4;
}
.workUl {
  margin: 25% 0 3% 0;
  padding: 0;
  li {
    padding: 5px 10px;
    list-style: none;
  }
}
.workTime {
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 16px;
  color: #fff;
  text-align: left;
  .rectIcon {
    width: 20px;
    height: 20px;
    display: inline-block;
    transform: rotate(45deg);
    background: #5e615f;
  }
  .time {
    background: rgb(237, 85, 101);
    height: 20px;
    line-height: 20px;
    margin-left: 20px;
  }
}
.workContent {
  overflow: auto;
  margin-left: 9px;
  border-left: 2px solid rgb(51, 122, 183);
  padding-bottom: 5px;
  padding-left: 20px;
  margin-top: 3%;
  text-align: left;
  strong {
    margin-top: 10px;
    display: block;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5rem;
    display: inline-block;
  }
  em {
    font-size: 14px;
    color: #f38c16;
    line-height: 2rem;
    font-style: normal;
  }
  p {
    font-size: 14px;
  }
}
.stepBox {
  height: 70%;
  margin: 20% 2% 10% 2%;
}
.personBox {
  width: 100%;
  height: 100%;
  position: relative;
}
.musicIcon {
  width: 50px;
  height: 50px;
  background: red;
  position: fixed;
  top: 2%;
  left: 2%;
  border-radius: 50%;
  animation: musicRotate 10s infinite;
  cursor: pointer;
  background: url("/static/imgs/xzq.jpeg");
  z-index: 999;
}
@keyframes musicRotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.skillBox {
  background: #d3d3d3;
}
.skills,
.works {
  font-family: "Cabin Sketch", arial, serif;
  font-size: 40px;
  margin: 5% 0;
  padding: 0;
}
.skills {
  text-shadow: 3px 3px 0 rgb(36, 108, 190);
  color: #ffd200;
}
.works {
  text-shadow: 3px 3px 0 rgb(214, 174, 63);
  color: rgb(99, 108, 231);
}
.barUl {
  margin: 0;
  padding: 1% 5%;
  li {
    list-style: none;
  }
}
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
}
.bgs {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.bg01 {
  background-image: url("/static/imgs/bg-2.jpeg");
  // opacity: 0.9;
}
.bg02 {
  background-image: url("/static/imgs/bg01.jpeg");
}
.personImg {
  margin-top: 10%;
  width: 150px;
  height: 150px;
  display: inline-block;
  background: url("../../static/imgs/luyaoguang.jpeg") no-repeat center center;
  background-size: cover;
  border: 1px solid #665353;
  border-radius: 50%;
}
.personInfo {
  width: 70%;
  margin: 10% 15%;
  color: #fff;
}
.clear {
  clear: both;
  overflow: hidden;
}
</style>




